import { SelectionModel } from "@angular/cdk/collections";
import { HttpParams } from "@angular/common/http";
import { Component, OnInit, ViewChild } from "@angular/core";
import { MatMenuTrigger } from "@angular/material/menu";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { Router } from "@angular/router";
import { Subscription, Subject, takeUntil } from "rxjs";
import { InitiateRequestsService } from "src/app/erp-admin/data/services/initiateRequest.service";
import { SnackbarService } from "src/app/shared/services/snackbar.service";
import Swal from "sweetalert2";

@Component({
  selector: "app-all-requests",
  templateUrl: "./all-requests.component.html",
  styleUrls: ["./all-requests.component.sass"],
})
export class AllRequestsComponent implements OnInit {

  displayedColumns: string[] = ["id", "username","firstName", "lastName", "empPfNo", "email", "phoneNumber", "role", "effectiveDate", "status","postedTime", "action"];

  dataSource!: MatTableDataSource<any>;
  @ViewChild(MatPaginator) paginator!: MatPaginator;
  @ViewChild(MatSort) sort!: MatSort;

  contextMenu: MatMenuTrigger;
  contextMenuPosition = { x: "0px", y: "0px" };
  subscription!: Subscription;
  selection = new SelectionModel<any>(true, []);
  data: any;
  error: any;
  formData: any;
  isLoading = true;
  pageFunction = "Update";
    destroy$: Subject<boolean> = new Subject<boolean>();

  downloadLoading: boolean = false;



  constructor(
    private router: Router,
    private snackbar: SnackbarService,
    private initiateRequestsService: InitiateRequestsService
  ) {}

  ngOnInit(): void {
    this.getData();
  }
  ngOnDestroy(): void {
    this.destroy$.next(true);
    this.destroy$.complete();
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  getData() {
    this.initiateRequestsService
      .getRequests()
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (res) => {
          console.log("res: ", res)
          if (res) {
            this.data = res;
            this.isLoading = false;
            this.dataSource = new MatTableDataSource(this.data.entity);
            this.dataSource.paginator = this.paginator;
            this.dataSource.sort = this.sort;
          } else {
            this.snackbar.showNotification("snackbar-danger", res);
          }
        },
        error: (err) => {
          console.log("err: ", err)
          this.snackbar.showNotification("snackbar-danger", err.message);
        },
        complete: () => {},
      }),
      Subscription;
  }

  addRequest() {
    this.router.navigate(["/erp-admin/admin/manage-request"]);
  }

  refresh() {
    this.getData();
  }

  editRequest(data: any) {
    const additionalData = data;
    const serializedData = JSON.stringify(additionalData);
    console.log("mm", data);

    let route = "/erp-admin/admin/manage-request";
    this.router.navigate([route], {
      queryParams: {
        id: data.id,
        additionalData: serializedData,
        action: "Update",
      },
    });
  }
  viewRequest(data: any) {
    const additionalData = data;
    const serializedData = JSON.stringify(additionalData);
    let route = "/erp-admin/admin/manage-request";
    this.router.navigate([route], {
      queryParams: {
        id: data.id,
        additionalData: serializedData,
        action: "View",
      },
    });
  }

  deleteRequest(id: number) {
    Swal.fire({
      title: "Are you sure?",
      text: "Do you really want to delete this request?",
      icon: "question",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, Delete Request!",
    }).then((result) => {
      if (result.isConfirmed) {
        const params = new HttpParams().set("id", id);

        this.initiateRequestsService
          .deleteRequestTemporarily(params)
          .pipe(takeUntil(this.destroy$))
          .subscribe({
            next: (res) => {
              this.snackbar.showNotification(
                "snackbar-success",
                res.message
              );
            },
            error: (err) => {
              this.snackbar.showNotification(
                "snackbar-danger",
               err.message
              );
            },
            complete: () => {
              this.getData();
            },
          }),
          Subscription;
      }
    });
  }
}
