  // ****************************************************************************************************************************
  constructor(
    private fb: FormBuilder,
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private tokenStorageService: TokenStorageService,
    private emailService: EmailService,
    private snackbar: SnackbarService,
    private route: ActivatedRoute,
    private notificationAPI: NotificationService,
  ) {
    this.currentUser = this.tokenStorageService.getUser();
  }

  // ****************************************************************************************************************************
  //define actions on component initialization
  ngOnInit(): void {
    this.getPage();
    this.showForm = true;

    this.route.queryParams.subscribe((params) => {
      //  Confirm the received params from the parent component in path
      console.log("res", params);

      if (params.hasOwnProperty("id")) {
        const action = params["action"];
        if (action == "Update") {
          this.pageFunction = "Update";
        } else if (action == "View") {
          this.pageFunction = "View";
        } else if (action == "Verify") {
          this.pageFunction = "Verify";
        }

        this.id = params["id"];

        this.getDataById();
      }
    });
  }

  // ****************************************************************************************************************************
  //definition of data destruction ones data utility is complete
  ngOnDestroy(): void {
    this.destroy$.next(true);
    this.destroy$.complete();
  }
  loading: boolean = false;
  getDataById() {
    this.loading = true
    const params = new HttpParams().set("id", this.id);
    this.emailService
      .getEmailById(params)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (res) => {
          console.log("res :", res);
          if (res.entity) {
            this.formData = res.entity;

            this.showForm = true;

            this.getPage();

            console.log("this.formData :", this.formData);
          } else {
            this.snackbar.showNotification("snackbar-danger", res.message);
          }
        },
        error: (err) => {
          this.snackbar.showNotification("snackbar-danger", err.message);
        },
        complete: () => { this.loading = false },
      }),
      Subscription;
  }

  //definition of necessary functions based on action in params
  getPage(): void {
    console.log("RES", this.formData);
    if (this.pageFunction === "Add") {
      this.generateForm();
      this.hideApprovals = true;
    } else if (this.pageFunction === "Update") {
      this.generateFormWithData();
      this.hideApprovals = true;
    } else if (this.pageFunction === "View") {
      this.generateDisabledFormWithData();
      this.hideSubmit = true;
      this.hideApprovals = true;
    } else if (this.pageFunction === "Verify") {
      this.generateDisabledFormWithData();
      this.hideSubmit = true;
      this.hideApprovals = false;
    }
  }

  // ****************************************************************************************************************************
  //defining the pages behavior based on action in params
  generateForm(): void {
    this.mngForm = this.fb.group({
      // emailMessage: ['', Validators.required],
      // emailRegards: ['', Validators.required],
      // emailRemarks: ['', Validators.required],
      port: [0, [Validators.required, Validators.min(0)]],
      host: ['', Validators.required],
      protocol: ['', Validators.required],
      smtpPassword: ['', Validators.required],
      smtpUsername: ['', Validators.required],
      sslTrust: ['', Validators.required],
      imageBanner: ['', Validators.required],
      imageLogo: ['', Validators.required],

    });
  }

