import { Component, OnInit, ViewChild } from "@angular/core";
import {
  FormGroup,
  FormBuilder,
  Validators,
  AbstractControl,
  FormArray,
} from "@angular/forms";
import { MatDialog, MatDialogConfig } from "@angular/material/dialog";
import { Router, ActivatedRoute } from "@angular/router";
import { Subject, takeUntil, Subscription } from "rxjs";
import { TokenStorageService } from "src/app/core/service/token-storage.service";
import { UsersManagementService } from "src/app/erp-admin/data/services/user-management.service";
import { CcentreLookupComponent } from "src/app/erp-finance/data/lookups/ccentre-lookup/ccentre-lookup.component";
import { SnackbarService } from "src/app/shared/services/snackbar.service";

import * as XLSX from "xlsx";
import { RolesLookupComponent } from "../../roles/dialogs/roles-lookup/roles-lookup.component";
import { HttpParams } from "@angular/common/http";
import { BranchesLookupComponent } from "src/app/erp-hr/hr-lookups/configurations-lookups/branches-lookup/branches-lookup.component";
import { DepartmentsLookupComponent } from "src/app/erp-hr/hr-lookups/configurations-lookups/departments-lookup/departments-lookup.component";

@Component({
  selector: "app-manage-user",
  templateUrl: "./manage-user.component.html",
  styleUrls: ["./manage-user.component.sass"],
})
export class ManageUserComponent implements OnInit {
  showForm = false;
  pageFunction = "Add";
  mngForm!: FormGroup;
  currentUser: any;
  formData: any;
    destroy$: Subject<boolean> = new Subject<boolean>();

  downloadLoading: boolean = false;
  id: any;
  hideSubmit = false;
  constructor(
    private fb: FormBuilder,
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private tokenStorageService: TokenStorageService,
    private usersManagementService: UsersManagementService,
    private snackbar: SnackbarService,
    private route: ActivatedRoute,
    private dialog: MatDialog
  ) {
    this.currentUser = this.tokenStorageService.getUser().username;
  }

  ngOnInit(): void {
    this.getPage();
    this.showForm = true;

    console.log("this.formData: ", this.formData);

    this.route.queryParams.subscribe((params) => {
      //  Confirm the received params from the parent component in path
      console.log("res", params);

      if (params.hasOwnProperty("id")) {
        const action = params["action"];
        if (action == "Update") {
          this.pageFunction = "Update";
        } else if (action == "View") {
          this.pageFunction = "View";
        } 
        this.id = params["id"];

        this.getDataById();
      }
    });
  }

  ngOnDestroy(): void {
    this.destroy$.next(true);
    this.destroy$.complete();
  }

  getDataById() {
    const params = new HttpParams().set("id", this.id);
    this.usersManagementService
      .getUsersById(params)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (res) => {
          console.log("res :", res);
          if (res.entity) {
            this.formData = res.entity;

            this.showForm = true;

            this.getPage();

            console.log("this.formData :", this.formData);
          } else {
            this.snackbar.showNotification("snackbar-danger", res.message);
          }
        },
        error: (err) => {
          this.snackbar.showNotification("snackbar-danger", err.message);
        },
        complete: () => {},
      }),
      Subscription;
  }


  getPage(): void {
    console.log("RES", this.formData);
    if (this.pageFunction === "Add") {
      this.generateForm();
    } else if (this.pageFunction === "Update") {
      this.generateFormWithData();
    } else if (this.pageFunction === "View") {
      this.generateDisabledFormWithData();
      this.hideSubmit = true;
    }
  }

  generateForm(): void {
    this.mngForm = this.fb.group({
      branchCode: ["", Validators.required],
      departmentCode: ["", Validators.required],
      costCenterCode: ["", Validators.required],
      email: ["", [Validators.required, Validators.email]],
      empPfNo: ["", Validators.required],
      firstName: ["", Validators.required],
      lastName: ["", Validators.required],
      phoneNo: ["", Validators.required],
      roleName: ["", Validators.required],
      username: ["", Validators.required],
    });
  }

  generateFormWithData(): void {
    this.mngForm = this.fb.group({
      id: [this.formData.id, Validators.required],
      branchCode: [this.formData.branchCode, Validators.required],
      departmentCode: [this.formData.departmentCode, Validators.required],
      costCenterCode: [this.formData.costCenterCode, Validators.required],
      email: [this.formData.email, [Validators.required, Validators.email]],
      empPfNo: [this.formData.empPfNo, Validators.required],
      firstName: [this.formData.firstName, Validators.required],
      lastName: [this.formData.lastName, Validators.required],
      phoneNo: [this.formData.phoneNo, Validators.required],
      roleName: [this.formData.roleName, Validators.required],
      username: [this.formData.username, Validators.required],
    });
  }

  generateDisabledFormWithData(): void {
    this.mngForm = this.fb.group({
      id: [{ value: this.formData.id, disabled: true }, Validators.required],
      branchCode: [{ value: this.formData.branchCode, disabled: true }, Validators.required],
      departmentCode: [{ value: this.formData.departmentCode, disabled: true }, Validators.required],
      costCenterCode: [{ value: this.formData.costCenterCode, disabled: true }, Validators.required],
      email: [{ value: this.formData.email, disabled: true }, [Validators.required, Validators.email]],
      empPfNo: [{ value: this.formData.empPfNo, disabled: true }, Validators.required],
      firstName: [{ value: this.formData.firstName, disabled: true }, Validators.required],
      lastName: [{ value: this.formData.lastName, disabled: true }, Validators.required],
      phoneNo: [{ value: this.formData.phoneNo, disabled: true }, Validators.required],
      roleName: [{ value: this.formData.roleName, disabled: true }, Validators.required],
      username: [{ value: this.formData.username, disabled: true }, Validators.required],
    });
  }


  //****************************************************************************************************************************** */
  //Lookups
  //****************************************************************************************************************************** */
  //Branches
  branchIsSelected = false;
  branchesLookup() {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = false;
    dialogConfig.autoFocus = true;
    dialogConfig.width = "700px";
    dialogConfig.data = {
      action: "single branch",
    };

    const dialogRef = this.dialog.open(BranchesLookupComponent, dialogConfig);

    dialogRef.afterClosed().subscribe((result) => {
      if (result && result.data.length != 0) {
        console.log("result: ", result.data);

        this.branchIsSelected = true;

        this.mngForm.patchValue({
          branchCode: result.data[0].branchCode,
        });
      }
    });
  }

  //Departments
  departmentSelected = false;
  departmentsLookup() {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = false;
    dialogConfig.autoFocus = true;
    dialogConfig.width = "700px";
    dialogConfig.data = {
      action: "single department",
    };

    const dialogRef = this.dialog.open(
      DepartmentsLookupComponent,
      dialogConfig
    );

    dialogRef.afterClosed().subscribe((result) => {
      if (result && result.data.length != 0) {
        console.log("result: ", result.data);

        this.departmentSelected = true;

        this.mngForm.patchValue({
          departmentCode: result.data[0].departmentCode,
        });
      }
    });
  }
  //Costcenters
  costcenterIsSelected = false;
  costCentersLookup() {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = false;
    dialogConfig.autoFocus = true;
    dialogConfig.width = "700px";
    dialogConfig.data = {
      action: "single costcenter",
    };

    const dialogRef = this.dialog.open(CcentreLookupComponent, dialogConfig);

    dialogRef.afterClosed().subscribe((result) => {
      if (result && result.data.length != 0) {
        console.log("result: ", result.data);

        this.costcenterIsSelected = true;

        this.mngForm.patchValue({
          costCenterCode: result.data[0].costCenterCode,
        });
      }
    });
  }


  //Roles
  roleIsSelected = false;
  rolesLookup() {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = false;
    dialogConfig.autoFocus = true;
    dialogConfig.width = "700px";
    dialogConfig.data = {
      action: "single role",
    };

    const dialogRef = this.dialog.open(RolesLookupComponent, dialogConfig);

    dialogRef.afterClosed().subscribe((result) => {
      if (result && result.data.length != 0) {
        console.log("result: ", result.data);

        this.roleIsSelected = true;

        this.mngForm.patchValue({
          roleName: result.data[0].name,
        });
      }
    });
  }
  // *************************************************************************************************************************************
  //Import from Excel
  @ViewChild("fileInput") fileInput: any;

  items: any[] = []; // Array to store
  itemsForm: FormGroup;
  itemErrors: { [key: string]: string }[] = [];

  handleFileInput(files: FileList) {
    const file = files.item(0);

    if (file) {
      const fileReader = new FileReader();
      fileReader.onload = (e: any) => {
        const arrayBuffer = e.target.result;
        const workbook = XLSX.read(arrayBuffer, { type: "array" });
        const worksheet = workbook.Sheets[workbook.SheetNames[0]];
        const jsonData = XLSX.utils.sheet_to_json(worksheet, { raw: true });

        this.items = jsonData;

        // this.items = [
        //   {
        //     costCenterName: "rrewrew",
        //     costCenterCode: "33343",
        //   },
        // ];

        console.log(" this.items :", this.items);

        // Initialize the form
        this.itemsForm = this.fb.group({
          items: this.fb.array([]), // Array of items
        });

        // Call the function to validate the items
        this.validateItems();
      };

      fileReader.readAsArrayBuffer(file);
    }
  }

  validateItems() {
    this.itemErrors = [];
    const items = this.items;

    const itemsFormArray = this.itemsForm.get("items") as FormArray;

    for (const item of items) {
      const formGroup = this.fb.group({
        branchCode: [item.branchCode, [Validators.required]],
        departmentCode: [item.departmentCode, [Validators.required]],
        costCenterCode: [item.costCenterCode, [Validators.required]],
        email: [item.email, [Validators.required, Validators.email]],
        empPfNo: [item.empPfNo, [Validators.required]],
        firstName: [item.firstName, [Validators.required]],
        lastName: [item.lastName, [Validators.required]],
        phoneNo: [item.phoneNo, [Validators.required]],
        roleName: [item.roleName, [Validators.required]],
        username: [item.username, [Validators.required]],
      });
      

      itemsFormArray.push(formGroup);
      this.collectErrors(formGroup);

      if (formGroup.valid) {
      }
    }
  }

  collectErrors(control: AbstractControl, path: string = "") {
    if (control instanceof FormGroup) {
      Object.keys(control.controls).forEach((key) => {
        const subControl = control.get(key);
        const subPath = this.getErrorPath(path, key);
        this.collectErrors(subControl, subPath);
      });
    } else {
      const errors = control.errors;
      if (errors) {
        const itemError = {
          field: path,
          message: this.getErrorMessage(errors),
        };
        this.itemErrors.push(itemError);
      }
    }
  }

  getErrorPath(path: string, key: string): string {
    return path ? `${path}.${key}` : key;
  }

  getErrorMessage(errors: any): string {
    if (errors.required) {
      return "This field is required.";
    } else if (errors.pattern) {
      return "Invalid value.";
    }
    return "";
  }

  private readonly templateFilename = "UserManagement.xlsx";

  downloadTemplate() {
    const link = document.createElement("a");
    link.setAttribute("type", "hidden");
    link.href = `/assets/templates/${this.templateFilename}`;
    link.download = this.templateFilename;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  }
  //******************************************************************************************************************************** */
  cancel(): void {
    this.router.navigate(["/erp-admin/admin/all-users"]);
  }

  posting: boolean = false;
  submit() {
    console.log("this.mngForm.value: ", this.mngForm.value);
    this.posting = true;
    if (this.pageFunction === "Add") {
      this.usersManagementService
        .addUser(this.mngForm.value)
        .pipe(takeUntil(this.destroy$))
        .subscribe({
          next: (res) => {

            console.log("res:: ", res);

            if (res.entity) {
              this.snackbar.showNotification("snackbar-success", res.message);
              this.router.navigate(["/erp-admin/admin/all-users"]);
            } else {
              this.snackbar.showNotification("snackbar-danger", res.message);
            }
          },
          error: (err) => {
            this.snackbar.showNotification("snackbar-danger", err.message);
          },
          complete: () => {
            this.posting = false;
          },
        }),
        Subscription;
    } else if (this.pageFunction === "Update") {
      this.usersManagementService
        .updateUser(this.mngForm.value)
        .pipe(takeUntil(this.destroy$))
        .subscribe({
          next: (res) => {
            if (res) {
              this.snackbar.showNotification("snackbar-success", res.message);
              this.router.navigate(["/erp-admin/admin/all-users"]);
            } else {
              this.snackbar.showNotification("snackbar-danger", res.message);
            }
          },
          error: (err) => {
            this.snackbar.showNotification("snackbar-danger", err.message);
          },
          complete: () => {
            this.posting = false;
          },
        }),
        Subscription;
    }
  }


  //Uploading a batch of items
  submitBatch() {
    this.posting = true;
    if (this.pageFunction === "Add") {
      this.usersManagementService
        .uploadBatchUsers(this.items)
        .pipe(takeUntil(this.destroy$))
        .subscribe({
          next: (res) => {
            if (res.statusCode == 201 || 200 || 302) {
              this.snackbar.showNotification(
                "snackbar-success",
                res.message
              );
              this.router.navigate([
                "/erp-finance/finance-parameters/all-CCentres",
              ]);
            } else {
              this.snackbar.showNotification("snackbar-danger", res.message);
            }
          },
          error: (err) => {
            this.snackbar.showNotification("snackbar-danger", err.message);
          },
          complete: () => { this.posting = false },
        }),
        Subscription;
    }
  }
}
