  addDelegation() {
    this.router.navigate(["/erp-admin/admin/manage-delegations"]);
  }


  editRole(data: any) {
    const additionalData = data;
    //const serializedData = JSON.stringify(additionalData);
    let route = '/erp-admin/admin/manage-delegations';
    this.router.navigate([route], { queryParams: { id: data.id, action: 'Update' } });
  }
  viewRole(data: any) {
    const additionalData = data;
    //const serializedData = JSON.stringify(additionalData);
    let route = '/erp-admin/admin/manage-delegations';
    this.router.navigate([route], { queryParams: { id: data.id, action: 'View' } });
  }
  validateRole(data: any) {
    const additionalData = data;
    // const serializedData = JSON.stringify(additionalData);
    let route = '/erp-admin/admin/manage-delegations';
    this.router.navigate([route], { queryParams: { id: data.id, action: 'Validate' } });
  }




  deleteRole(id: any) {
    Swal.fire({
      title: "Are you Sure?",
      text: "This Role will be deleted!!",
      icon: "question",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      confirmButtonText: "Yes, Delete this Role ",
    }).then((result) => {
      if (result.isConfirmed) {
        let params = { id: id }
        this.delegationService
          .deleteDelegationTemporarily(params)
          .pipe(takeUntil(this.destroy$))
          .subscribe({
            next: (res) => {
              console.log("res: ", res);
              if (res.statusCode == 201 || 200 || 302) {
                this.snackbar.showNotification(
                  "snackbar-success",
                  res.message
                );
              } else {
                this.snackbar.showNotification(
                  "snackbar-danger",
                  res.message
                );
              }
            },
            error: (err) => {
              this.snackbar.showNotification(
                "snackbar-danger",
                err.message
              );
            },
            complete: () => {
              this.getData();
            },
          }),
          Subscription;
      }
    });
  }

  // ****************************************************************************************************************************

  //Select
  selectedRows: any[] = [];
  atleastOneSelected: boolean = false;

  /** Whether the number of selected elements matches the total number of rows. */
  isAllSelected() {
    const numSelected = this.selection.selected.length;
    const numRows = this.dataSource.data.length;
    return numSelected === numRows;
  }

  /** Selects all rows if they are not all selected; otherwise clear selection. */
  masterToggle() {
    if (this.isAllSelected()) {
      this.selection.clear();
      return;
    }

    this.selection.select(...this.dataSource.data);
  }

  /** The label for the checkbox on the passed row */
  checkboxLabel(row?: any): string {
    if (!row) {
      return `${this.isAllSelected() ? "deselect" : "select"} all`;
    }
    return `${this.selection.isSelected(row) ? "deselect" : "select"} row ${row.position + 1
      }`;
  }

  expSelected() {
    this.atleastOneSelected = true;
    this.selectedRows = this.selection.selected;

    console.log("this.selectedRows: ", this.selectedRows);
  }

  hasAccess: boolean;
  processMultipleRows(status: any) {
    let privilege = "Dashboard";
    //Validate Users

    this.hasAccess = this.accessControlService.hasPrivilege("AdminModule", [
      privilege,
    ]);

    if (this.hasAccess) {
      Swal.fire({
    title: '<span style="font-size: 20px;">Enter Verifier Remarks</span>',
    input: "textarea",
    inputPlaceholder: "Type your remarks here...",
    confirmButtonText: "Submit",
    showCancelButton: true,
    cancelButtonText: "Cancel",
    icon: "info",

    inputValidator: (value) => {
          if (!value) {
            return "Please enter your remarks";
          }
        },
      }).then((result) => {
        if (result.isConfirmed) {
          const verifierRemarks = result.value;

          const processedRows = this.selectedRows.map((element) => {
            return {
              id: element.id,
              status: status,
              verifierRemarks: verifierRemarks,
            };
          });

          console.log("LOG: ", processedRows);

          this.delegationService
            .validateDelegations(processedRows)
            .pipe(takeUntil(this.destroy$))
            .subscribe({
              next: (res) => {
                console.log("res: ", res);
                if (res.statusCode == 200) {
                  this.snackbar.showNotification(
                    "snackbar-success",
                    res.message
                  );

                  this.selection = new SelectionModel<any>(true, []);
                } else {
                  this.snackbar.showNotification(
                    "snackbar-danger",
                    res.message
                  );
                }
              },
              error: (err) => {
                this.snackbar.showNotification("snackbar-danger", err.message);
              },
              complete: () => {
                this.getData();
              },
            }),
            Subscription;
        } else {
          this.selection = new SelectionModel<any>(true, []);
        }
      });
    }
  }
