import { HttpParams } from '@angular/common/http';
import { Component, OnInit, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { Router } from '@angular/router';
import { AccessRequestService } from 'src/app/erp-admin/data/services/accessRequest.service';
import { NotificationService } from 'src/app/erp-procurement/data/services/notification.service';

@Component({
  selector: 'app-access-request',
  templateUrl: './access-request.component.html',
  styleUrls: ['./access-request.component.sass']
})
export class AccessRequestComponent implements OnInit {

  displayedColumns: string[] = [
    'id',
    'firstName',
    'lastName',
    'empPfNo',
    'username',
    'role',
    'effectiveDate',
    'status',
    'postedTime',
    'action'
  ];  
  dataSource!: MatTableDataSource<any>;
  @ViewChild(MatPaginator) paginator!: MatPaginator;
  @ViewChild(MatSort) sort!: MatSort;
  isLoading: boolean;
  data: any;
  dataRes: any;
  dialogConfig: any;
  dataResp: any;

  downloadLoading: boolean = false;

  
  constructor(
    private accessRequestApi: AccessRequestService,
    private router: Router,
    private notificationAPI: NotificationService
  ) {
    this.getData();
   }
  ngOnDestroy(): void {
  }
  ngOnInit() {}

  refresh(){
    this.getData();
  }
  getData() {
    this.isLoading = true;
    this.accessRequestApi.findAll().subscribe(res=>{
      this.dataRes = res;
      console.log("this.dataRes: ", this.dataRes)
      if (this.dataRes.statusCode == 200) {
            this.isLoading= false;
            this.dataRes =  this.dataRes .entity;
            this.dataSource = new MatTableDataSource(this.dataRes);
            this.dataSource.paginator = this.paginator;
            this.dataSource.sort = this.sort;
      }else{
        this.notificationAPI.alertWarning(this.dataRes.message)
        this.isLoading= false;
      }
    }, err=>{
      this.notificationAPI.alertWarning(err)
      this.isLoading= false;
    });
  }
  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  } 
  onApprove(e:any){
    this.isLoading = true;
    if (window.confirm('ARE YOU SURE YOU WANT TO CREATE ACCOUNT FOR THIS?')) {
      let params = new HttpParams()
      .set('id',e);
      this.accessRequestApi.approve(params).subscribe(res=>{
        this.dataResp = res;
        this.notificationAPI.alertSuccess(this.dataResp.message)
        this.getData
      }, err=>{
      })
    }
  }
  onView(row: any, action: any) {
    this.router.navigate([`/erp-admin/admin/access-request-details`], { skipLocationChange: true, queryParams: { id: row.id, action: action } });
  }
}
