import { HttpParams } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AccessRequestService } from 'src/app/erp-admin/data/services/accessRequest.service';
import { RolesService } from 'src/app/erp-admin/data/services/roleService.service';
import { NotificationService } from 'src/app/erp-procurement/data/services/notification.service';
import Swal from 'sweetalert2'


@Component({
  selector: 'app-access-request-details',
  templateUrl: './access-request-details.component.html',
  styleUrls: ['./access-request-details.component.scss']
})
export class AccessRequestDetailsComponent implements OnInit {

  loading: boolean;
  data: any;
  dataResp: any;
  dataRes: any[];
  dialogConfig: any;
  jsonData: any;
  searchText: string;
  
  id = null;
  selectedItem: any;
  firstName: any;
  lastName: any;
  username: any;
  empPfNo: any;
  email: any;
  phoneNumber: any;
  role: any;
  effectiveDate: any;
  otherDescription: any;
  disableSubmit=false;
  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private accessRequestApi: AccessRequestService,
    private notificationAPI: NotificationService,
    private roleAPi: RolesService
  ) {
    this.jsonData = this.route.snapshot.queryParams;
   
   }
  ngOnDestroy(): void {
  }
  ngOnInit() {
    this.getRoles();
    this.getData();
  }
  
  getRoles(){
    let params = new HttpParams()
    .set('deletedFlag','N');
    this.roleAPi.getRolesByStatus(params).subscribe(res=>{
      this.dataResp = res;
      this.dataRes = this.dataResp.entity;
    }, err=>{
    })
  }

  getData(){
    this.loading = true;
    this.id = this.jsonData.id;
    if(this.id != null){
      let params = new HttpParams()
      .set('id', this.id);
      this.accessRequestApi.findById(params).subscribe(res=>{
        this.dataResp = res;
        this.dataResp = this.dataResp.entity;
        this.firstName = this.dataResp.firstName;
        this.lastName = this.dataResp.lastName;
        this.username = this.dataResp.username;
        this.empPfNo = this.dataResp.empPfNo;
        this.email = this.dataResp.email;
        this.phoneNumber = this.dataResp.phoneNumber;
        this.role = this.dataResp.role;
        this.effectiveDate = this.dataResp.effectiveDate;
        this.otherDescription = this.dataResp.otherDescription;
        this.loading = false;
      }, err=>{
        this.loading = false;
      })
    }else{
      this.router.navigate([`users/access-request`], { skipLocationChange: true});
    }
  }

  onSelect(item: any) {
    this.selectedItem = item;
  }

  onSubmit(){
    this.loading = true;
    this.disableSubmit = true;
    if(this.selectedItem.id != null){

      Swal.fire({
        title: "Are you sure?",
        text: "Do you really want to verify this user?",
        icon: "question",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Yes, verify user!",
      }).then((result) => {
        if (result.isConfirmed) {
          // Perform the submit action here
          let params = new HttpParams()
          .set('id',this.id)
          .set('roleFk',this.selectedItem.id);
          this.accessRequestApi.createAccount(params).subscribe(res=>{
            this.dataResp = res;
            this.notificationAPI.alertSuccess(this.dataResp.message)
            this.disableSubmit = false;
            this.router.navigate([`/erp-admin/admin/access-request`], { skipLocationChange: true});
          }, err=>{
          })
          
        }
      });

     
    }


  }
  onReject(){
    
  }
}
