import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { Observable } from "rxjs";
import { environment } from "src/environments/environment.prod";



@Injectable({
  providedIn: "root",
})
export class DelegationService {

  constructor(private http: HttpClient) { }

  getDelegations(): Observable<any>
  {
    const DelegationsUrl =`${environment.baseUrlAdmin}/api/v1/auth/role/delegates/all`; 
    return this.http.get<any[]>(DelegationsUrl);
  }

  getDelegationsById(params: any): Observable<any> {
    return this.http.get<any>(
      `${environment.baseUrlAdmin}/api/v1/auth/role/delegates/find/by/id`, {params}
    );
  }

  getDelegationsByStatus(params): Observable<any> {
    const DelegationsUrl = `${environment.baseUrlAdmin}/api/v1/auth/role/delegates/fetchbyStatus`;
    return this.http.get<any[]>(DelegationsUrl, { params });
  }


addDelegation(data: any): Observable<any>
  {
    const DelegationsUrl =`${environment.baseUrlAdmin}/api/v1/auth/role/delegates/create`; 
    return this.http.post<any[]>(DelegationsUrl, data);
  }

updateDelegation(data: any): Observable<any>
  {
    const DelegationsUrl =`${environment.baseUrlAdmin}/api/v1/auth/role/delegates/update`; 
    return this.http.put<any[]>(DelegationsUrl, data);
  }

deleteDelegationTemporarily(params: any): Observable<any>
  {
    const DelegationsUrl =`${environment.baseUrlAdmin}/api/v1/auth/role/delegates/delete`; 
    return this.http.delete<any[]>(DelegationsUrl, {params});
  }


uploadBatchDelegations(data: any): Observable<any> 
  {
    const DelegationsUrl = `${environment.baseUrlAdmin}/api/v1/auth/role/delegates/create/bulk`;
    return this.http.post<any>(DelegationsUrl, data);
  }



validateDelegations(dataToValidate: any): Observable<any> {
const imprestUrl = `${environment.baseUrlAdmin}/api/v1/auth/role/delegates/update/state`;
return this.http.put<any>(imprestUrl, dataToValidate);
}


  getPrivielgesByParams(params: any): Observable<any> {
    const DelegationssUrl = `${environment.baseUrlAdmin}/api/v1/allprivileges/read`;
    return this.http.get<any>(DelegationssUrl, {params});
  }





    // ********************************************************************************************************************



}
