  mapExistingPrivileges() {
    this.clients = this.fetchedFormData.clients;

    console.log("this.clients: ", this.clients);

    // Clear existing privileges
    this.adminPrivilegesArray = [];
    this.procurementPrivilegesArray = [];
    this.hrPrivilegesArray = [];
    this.financePrivilegesArray = [];
    this.inventoryPrivilegesArray = [];
    this.prepaymentPrivilegesArray = [];
    this.fixedAssetsManagementPrivilegesArray = [];
    this.supplierManagementPrivilegesArray = [];
    this.budgetManagementPrivilegesArray = [];
    this.imprestManagementPrivilegesArray = [];
    this.employeePortalPrivilegesArray = [];

    // Map existing privileges to corresponding arrays
    this.clients.forEach((module) => {
      const privilegeArray = this.getPrivilegeArrayByName(module.name);
      if (privilegeArray) {
        module.privileges.forEach((privilege) =>
          privilegeArray.push(privilege)
        );
      }
    });
  }

  getPrivilegeArrayByName(moduleName: string) {
    switch (moduleName) {
      case "AdminModule":
        return this.adminPrivilegesArray;
      case "ProcurementModule":
        return this.procurementPrivilegesArray;
      case "HumanResourceModule":
        return this.hrPrivilegesArray;
      case "FinanceModule":
        return this.financePrivilegesArray;
      case "InventoryModule":
        return this.inventoryPrivilegesArray;
      case "PrepaymentModule":
        return this.prepaymentPrivilegesArray;
      case "FixedAssetsModule":
        return this.fixedAssetsManagementPrivilegesArray;
      case "SuppliersManagementModule":
        return this.supplierManagementPrivilegesArray;
      case "BudgetModule":
        return this.budgetManagementPrivilegesArray;
      case "ImprestModule":
        return this.imprestManagementPrivilegesArray;
      case "EmployeePortalModule":
        return this.employeePortalPrivilegesArray;
      default:
        return null;
    }
  }

