  updateSelectedItems(privilege: any, clientId: number) {
    if (privilege.selected) {
      this.selectedItems.push({
        clientId: clientId,
        privilegeId: privilege.id,
      });
    } else {
      this.selectedItems = this.selectedItems.filter(
        (item) =>
          !(item.clientId === clientId && item.privilegeId === privilege.id)
      );
    }

    console.log("this.selectedItems: ", this.selectedItems);
  }

  onSubmit() {
    Swal.fire({
      title: "Are you sure?",
      text: "Do you really want to " + this.pageAction + " this Privilege?",
      icon: "question",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, " + this.pageAction + " Privilege!",
    }).then((result) => {
      if (result.isConfirmed) {
        // Perform the submit action here

        if (this.formData.valid) {
          this.loading = true;
          console.log("this.loading: ", this.loading);
          if (this.pageAction === "Add") {
            let params = { serviceFk: this.formData.value.serviceFk };
            console.log("[this.formData.value]: ", [this.formData.value]);
            this.privilegeService.addPrivilege(params,[this.formData.value]).subscribe(
              (res) => {
                this.dataRes = res;
                console.log("this.dataRes: ", this.dataRes);
                if (this.dataRes.statusCode === 201) {
                  this.snackbarAPI.alertSuccess(this.dataRes.message);
                  this.router.navigate([`/erp-admin/dashboard`]);
                } else {
                  this.snackbarAPI.alertWarning(this.dataRes.message);
                }
                this.loading = false;
              },
              (err) => {
                this.loading = false;
              }
            );
          } else if (this.pageAction === "Update") {
            this.privilegeService
              .updatePrivilege([this.formData.value])
              .subscribe(
                (res) => {
                  this.dataRes = res;
                  if (this.dataRes.statusCode === 200) {
                    this.snackbarAPI.alertSuccess(this.dataRes.message);
                    this.router.navigate([`/erp-admin/dashboard`]);
                  } else {
                    this.snackbarAPI.alertWarning(this.dataRes.message);
                  }
                  this.loading = false;
                },
                (err) => {
                  this.loading = false;
                }
              );
          }
        } else {
          this.snackbarAPI.alertWarning("Invalid Form Data");

          this.loading = false;
        }
      }
    });
  }

  deletePrivileges() {
    Swal.fire({
      title: "Are you Sure?",
      text: "The selected privileges will be deleted!!",
      icon: "question",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      confirmButtonText: "Yes, Delete selected privileges ",
    }).then((result) => {
      if (result.isConfirmed) {
        this.loading = true;
        console.log("body: ", this.selectedItems);
        this.privilegeService
          .deletePrivilegeTemporarily(this.selectedItems)
          .pipe(takeUntil(this.destroy$))
          .subscribe({
            next: (res) => {
              console.log("res: ", res);
              if (res.statusCode == 201 || 200 || 302) {
                this.snackbar.showNotification("snackbar-success", res.message);
              } else {
                this.snackbar.showNotification("snackbar-danger", res.message);
              }
            },
            error: (err) => {
              this.snackbar.showNotification("snackbar-danger", err.message);
            },
            complete: () => {
              this.loading = false;

              this.getPrivileges();
              this.selectedItems = [];
            },
          }),
          Subscription;
      }
    });
  }

  // ******************************************************************************************************
  isDisabled: boolean;
  showBtn = true;

  selectedItems: any[] = [];
  toggleAll(selected: boolean, client: string, clientId: number) {
    const privilegesArray = this.getPrivilegeArrayByName(client);

    if (privilegesArray) {
      privilegesArray.forEach((privilege) => (privilege.selected = selected));

      if (selected) {
        privilegesArray.forEach((privilege) => {
          this.selectedItems.push({
            clientId: clientId,
            privilegeId: privilege.id,
          });
        });
      } else {
        this.selectedItems = this.selectedItems.filter(
          (item) => item.clientId !== clientId
        );
      }
    }

    console.log("this.selectedItems: ", this.selectedItems);
  }

  allSelected(client: string, clientId: number): boolean {
    const privilegesArray = this.getPrivilegeArrayByName(client);
    return privilegesArray
      ? privilegesArray.every((privilege) => privilege.selected)
      : false;
  }

