  generateExcel() {
    const workbook = new ExcelJS.Workbook();
    const worksheet = workbook.addWorksheet("Data");

    let rowIndex = 1;

    // Iterate over the categories
    const categories = Object.keys(this.allPriveleges);
    const maxItems = Math.max(
      ...categories.map((category) => this.allPriveleges[category].length)
    );

    // Write the column headers with category names bolded and spaced
    for (let i = 0; i < categories.length; i++) {
      const columnHeader = worksheet.getCell(
        `${this.getColumnIndex(3 * i)}${rowIndex}`
      );
      columnHeader.value = categories[i];
      columnHeader.font = { bold: true };

      for (let j = 1; j <= 2; j++) {
        const columnHeaderExtraSpace = worksheet.getCell(
          `${this.getColumnIndex(3 * i + j)}${rowIndex}`
        );
        columnHeaderExtraSpace.value = "";
      }
    }
    rowIndex++;

    // Populate the names of each item in each category with one column space
    for (let i = 0; i < maxItems; i++) {
      for (let j = 0; j < categories.length; j++) {
        const items = this.allPriveleges[categories[j]];
        if (items[i]) {
          worksheet.getCell(`${this.getColumnIndex(3 * j)}${rowIndex}`).value =
            items[i].name;
        }
      }
      rowIndex++;
    }

    workbook.xlsx.writeBuffer().then((buffer: ArrayBuffer) => {
      this.saveAsExcelFile(buffer, "data");
    });
  }

  // Helper method to get column letter from index (A, B, C, ..., Z, AA, AB, ...)
  getColumnIndex(index: number): string {
    let columnIndex = "";
    while (index >= 0) {
      columnIndex =
        String.fromCharCode("A".charCodeAt(0) + (index % 26)) + columnIndex;
      index = Math.floor(index / 26) - 1;
    }
    return columnIndex;
  }

  saveAsExcelFile(buffer: ArrayBuffer, fileName: string): void {
    const data: Blob = new Blob([buffer], {
      type: "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet",
    });
    const downloadLink: HTMLAnchorElement = document.createElement("a");
    const url: string = window.URL.createObjectURL(data);

    downloadLink.href = url;
    downloadLink.download = fileName + ".xlsx";
    document.body.appendChild(downloadLink);
    downloadLink.click();
    document.body.removeChild(downloadLink);
    window.URL.revokeObjectURL(url);
  }
