  onSelectInEmployeePortal(e: any, i: any) {
    this.employeePortalPrivilegesArray[i].selected = e.checked;
  }

  cleanUpIds(formData: any): any {
    const cleanRecursive = (data: any): any => {
      if (data === null || typeof data !== "object") {
        return data;
      }
      if (Array.isArray(data)) {
        // If it's an array, recursively clean each element
        return data.map((element) => cleanRecursive(element));
      }

      // Remove "id" field if it is null, undefined, or an empty string
      const cleanedData: any = {};
      for (const [key, value] of Object.entries(data)) {
        if (key === "id") {
          continue; // Skip "id" field
        }
        cleanedData[key] = cleanRecursive(value);
      }
      return cleanedData;
    };
    // Clone the form data to avoid modifying the original object
    const cleanedData = cleanRecursive({ ...formData });

    return cleanedData;
  }

  onSubmit() {
    Swal.fire({
      title: "Are you sure?",
      text:
        "Do you really want to " + this.pageAction + " this Role Delegation?",
      icon: "question",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, " + this.pageAction + " Role Delegation!",
    }).then((result) => {
      if (result.isConfirmed) {
        // Perform the submit action here

        this.loading = true;

        const privilegeArrays: { array: PrivilegeArray[] }[] = [
          { array: this.adminPrivilegesArray },
          { array: this.hrPrivilegesArray },
          { array: this.procurementPrivilegesArray },
          { array: this.financePrivilegesArray },
          { array: this.budgetManagementPrivilegesArray },
          { array: this.fixedAssetsManagementPrivilegesArray },
          { array: this.supplierManagementPrivilegesArray },
          { array: this.imprestManagementPrivilegesArray },
          { array: this.employeePortalPrivilegesArray }
        ];
        
        this.clients.forEach((client, index) => {
          client.selected = privilegeArrays[index].array.some(privilege => privilege.selected);
        });
        console.log("this.clients: ", this.clients);

        // Create clientsArray using map and forEach
        const clientsArray: Client[] = this.clients.map((client) => {
          const privilegeArray: any[] = [];

          // Find the corresponding privilege array for each client
          switch (client.name) {
            case "AdminModule":
              privilegeArray.push(...this.adminPrivilegesArray);
              break;
            case "HumanResourceModule":
              privilegeArray.push(...this.hrPrivilegesArray);
              break;
            case "ProcurementModule":
              privilegeArray.push(...this.procurementPrivilegesArray);
              break;
            case "FinanceModule":
              privilegeArray.push(...this.financePrivilegesArray);
              break;
            case "InventoryModule":
              privilegeArray.push(...this.inventoryPrivilegesArray);
              break;
            case "PrepaymentModule":
              privilegeArray.push(...this.prepaymentPrivilegesArray);
              break;
            case "BudgetModule":
              privilegeArray.push(...this.budgetManagementPrivilegesArray);
              break;
            case "FixedAssetsModule":
              privilegeArray.push(...this.fixedAssetsManagementPrivilegesArray);
              break;
            case "SuppliersManagementModule":
              privilegeArray.push(...this.supplierManagementPrivilegesArray);
              break;
            case "ImprestModule":
              privilegeArray.push(...this.imprestManagementPrivilegesArray);
              break;
            case "EmployeePortalModule":
              privilegeArray.push(...this.employeePortalPrivilegesArray);
              break;
            // Add cases for other modules if needed
            default:
              break;
          }

          return {
            ...client,
            privileges: privilegeArray,
          };
        });

        console.log("this.clientsArray: ", clientsArray);

        // Build the final json object
        const json: Role = {
          id: this.formData.value.id,
          fromPf: this.formData.value.fromPf,
          fromName: this.formData.value.fromName,
          toPf: this.formData.value.toPf,
          toName: this.formData.value.toName,
          clients: clientsArray,
        };

        console.log("json: ", json);

        const cleanedFormData = this.cleanUpIds(json);

        console.log("cleanedFormData: ", cleanedFormData);

        if (this.formData.valid) {
          if (this.pageAction === "Add") {
            this.delegationService.addDelegation(cleanedFormData).subscribe(
              (res) => {
                this.dataRes = res;
                if (this.dataRes.statusCode === 201) {
                  this.snackbarAPI.alertSuccess(this.dataRes.message);
                  this.router.navigate([`/erp-admin/admin/all-delegations`], {
                    skipLocationChange: true,
                  });
                } else {
                  this.snackbarAPI.alertWarning(this.dataRes.message);
                }
              },
              (err) => {}
            );
            this.loading = false;
          } else if (this.pageAction === "Update") {
            this.delegationService.updateDelegation(json).subscribe(
              (res) => {
                this.dataRes = res;
                if (this.dataRes.statusCode === 200) {
                  this.snackbarAPI.alertSuccess(this.dataRes.message);
                  this.router.navigate([`/erp-admin/admin/all-delegations`], {
                    skipLocationChange: true,
                  });
                } else {
                  this.snackbarAPI.alertWarning(this.dataRes.message);
                }
              },
              (err) => {}
            );
            this.loading = false;
          }
        } else {
          this.snackbarAPI.alertWarning("Invalid Form Data");
          this.loading = false;
        }
      }
    });
  }

  // ******************************************************************************************************
  isDisabled: boolean;
  showBtn = true;

  toggleAll(selected: boolean, client: string) {
    const privilegesArray = this.getPrivilegeArrayByName(client);

    if (privilegesArray) {
      privilegesArray.forEach((privilege) => (privilege.selected = selected));
    }
  }

  allSelected(client: string): boolean {
    const privilegesArray = this.getPrivilegeArrayByName(client);

    return privilegesArray
      ? privilegesArray.every((privilege) => privilege.selected)
      : false;
  }

  //*********************************************************************************************************** */
  cancel() {
    this.router.navigate([`/erp-admin/admin/all-delegations`]);
  }

  //****************************************************************************************************************************** */
  //Branches
  employeeIsSelected = false;
  employeesLookup(type: any) {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = false;
    dialogConfig.autoFocus = true;
    dialogConfig.width = "700px";
    dialogConfig.data = {
      action: "single employee",
    };

    const dialogRef = this.dialog.open(EmployeesLookupComponent, dialogConfig);

    dialogRef.afterClosed().subscribe((result) => {
      if (result && result.data.length != 0) {
        console.log("result: ", result.data);

        this.employeeIsSelected = true;

        if (type === "From") {
          this.formData.patchValue({
            fromPf: result.data[0].empNo,
            fromName: result.data[0].firstName,
          });
          this.getDataByPf(result.data[0].empNo);
        } else if (type === "To") {
          this.formData.patchValue({
            toPf: result.data[0].empNo,
            toName: result.data[0].firstName,
          });
        }
      }
    });
  }
