import { Component, OnInit } from "@angular/core";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { MatDialog, MatDialogConfig } from "@angular/material/dialog";
import { Router, ActivatedRoute } from "@angular/router";
import { Subject, takeUntil, Subscription } from "rxjs";
import { TokenStorageService } from "src/app/core/service/token-storage.service";
import { InitiateRequestsService } from "src/app/erp-admin/data/services/initiateRequest.service";
import { CcentreLookupComponent } from "src/app/erp-finance/data/lookups/ccentre-lookup/ccentre-lookup.component";
import { BranchesLookupComponent } from "src/app/erp-hr/hr-lookups/configurations-lookups/branches-lookup/branches-lookup.component";
import { DepartmentsLookupComponent } from "src/app/erp-hr/hr-lookups/configurations-lookups/departments-lookup/departments-lookup.component";
import { SnackbarService } from "src/app/shared/services/snackbar.service";

@Component({
  selector: "app-manage-request",
  templateUrl: "./manage-request.component.html",
  styleUrls: ["./manage-request.component.sass"],
})
export class ManageRequestComponent implements OnInit {
  showForm = false;
  pageFunction = "Add";
  mngForm!: FormGroup;
  currentUser: any;
  formData: any;
    destroy$: Subject<boolean> = new Subject<boolean>();

  downloadLoading: boolean = false;
  id: any;
  hideSubmit = false;
  constructor(
    private fb: FormBuilder,
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private tokenStorageService: TokenStorageService,
    private initiateRequestsService: InitiateRequestsService,
    private snackbar: SnackbarService,
    private route: ActivatedRoute,
    private dialog: MatDialog
  ) {
    this.currentUser = this.tokenStorageService.getUser().username;
  }

  ngOnInit(): void {
    this.getPage();
    this.showForm = true;

    console.log("this.formData: ", this.formData);

    this.route.queryParams.subscribe((params) => {
      console.log("res", params);
      if (params.hasOwnProperty("id")) {
        const action = params["action"];
        if (action == "Update") {
          this.pageFunction = "Update";
        } else if (action == "View") {
          this.pageFunction = "View";
        }

        this.id = params["id"];
        const serializedData = params["additionalData"];
        const additionalData = JSON.parse(serializedData);

        this.formData = additionalData;

        console.log("ID:", this.id);
        console.log("Additional Data:", additionalData);

        this.getPage();
      }
    });
  }

  ngOnDestroy(): void {
    this.destroy$.next(true);
    this.destroy$.complete();
  }

  getPage(): void {
    console.log("RES", this.formData);
    if (this.pageFunction === "Add") {
      this.generateForm();
    } else if (this.pageFunction === "Update") {
      this.generateFormWithData();
    } else if (this.pageFunction === "View") {
      this.generateDisabledFormWithData();
      this.hideSubmit = true;
    }
  }

  generateForm(): void {
    this.mngForm = this.fb.group({
      accessPrivileges: ["", Validators.required],
      branchCode: ["", Validators.required],
      departmentCode: ["", Validators.required],
      costCenterCode: ["", Validators.required],
      effectiveDate: ["", Validators.required],
      email: ["", [Validators.required, Validators.email]],
      empPfNo: ["", Validators.required],
      firstName: ["", Validators.required],
      lastName: ["", Validators.required],
      otherDescription: ["", Validators.required],
      phoneNumber: ["", Validators.required],
      role: ["", Validators.required],
      //status: ['', Validators.required],
      username: ["", Validators.required],
    });
  }

  generateFormWithData(): void {
    this.mngForm = this.fb.group({
      id: [this.formData.id],
      accessPrivileges: [this.formData.accessPrivileges, Validators.required],
      branchCode: [this.formData.branchCode, Validators.required],
      departmentCode: [this.formData.departmentCode, Validators.required],
      costCenterCode: [this.formData.costCenterCode, Validators.required],

      effectiveDate: [
        new Date(this.formData.effectiveDate),
        Validators.required,
      ],
      email: [this.formData.email, [Validators.required, Validators.email]],
      empPfNo: [this.formData.empPfNo, Validators.required],
      firstName: [this.formData.firstName, Validators.required],
      lastName: [this.formData.lastName, Validators.required],
      otherDescription: [this.formData.otherDescription, Validators.required],
      phoneNumber: [this.formData.phoneNumber, Validators.required],
      role: [this.formData.role, Validators.required],
      // status: [this.formData.status, Validators.required],
      username: [this.formData.username, Validators.required],
    });
  }

  generateDisabledFormWithData(): void {
    this.mngForm = this.fb.group({
      id: [{ value: this.formData.id, disabled: true }],
      accessPrivileges: [
        { value: this.formData.accessPrivileges, disabled: true },
        Validators.required,
      ],
      branchCode: [
        { value: this.formData.branchCode, disabled: true },
        Validators.required,
      ],
      departmentCode: [
        { value: this.formData.departmentCode, disabled: true },
        Validators.required,
      ],
      costCenterCode: [
        { value: this.formData.costCenterCode, disabled: true },
        Validators.required,
      ],

      effectiveDate: [
        { value: new Date(this.formData.effectiveDate), disabled: true },
        Validators.required,
      ],
      email: [
        { value: this.formData.email, disabled: true },
        [Validators.required, Validators.email],
      ],
      empPfNo: [
        { value: this.formData.empPfNo, disabled: true },
        Validators.required,
      ],
      firstName: [
        { value: this.formData.firstName, disabled: true },
        Validators.required,
      ],
      lastName: [
        { value: this.formData.lastName, disabled: true },
        Validators.required,
      ],
      otherDescription: [
        { value: this.formData.otherDescription, disabled: true },
        Validators.required,
      ],
      phoneNumber: [
        { value: this.formData.phoneNumber, disabled: true },
        Validators.required,
      ],
      role: [
        { value: this.formData.role, disabled: true },
        Validators.required,
      ],
      // status: [{ value: this.formData.status, disabled: true }, Validators.required],
      username: [
        { value: this.formData.username, disabled: true },
        Validators.required,
      ],
    });
  }

  //****************************************************************************************************************************** */
  //Lookups
  //****************************************************************************************************************************** */
  //Branches
  branchIsSelected = false;
  branchesLookup() {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = false;
    dialogConfig.autoFocus = true;
    dialogConfig.width = "700px";
    dialogConfig.data = {
      action: "single branch",
    };

    const dialogRef = this.dialog.open(
      BranchesLookupComponent,
      dialogConfig
    );

    dialogRef.afterClosed().subscribe((result) => {
      if (result && result.data.length != 0) {
        console.log("result: ", result.data);

        this.branchIsSelected = true;

        this.mngForm.patchValue({
          branchCode: result.data[0].branchCode,
        });
      }
    });
  }

  //Departments
  departmentSelected = false;
  departmentsLookup() {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = false;
    dialogConfig.autoFocus = true;
    dialogConfig.width = "700px";
    dialogConfig.data = {
      action: "single department",
    };

    const dialogRef = this.dialog.open(
      DepartmentsLookupComponent,
      dialogConfig
    );

    dialogRef.afterClosed().subscribe((result) => {
      if (result && result.data.length != 0) {
        console.log("result: ", result.data);

        this.departmentSelected = true;

        this.mngForm.patchValue({
          departmentCode: result.data[0].departmentCode,
        });
      }
    });
  }
  //Costcenters
  costcenterIsSelected = false;
  costCentersLookup() {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = false;
    dialogConfig.autoFocus = true;
    dialogConfig.width = "700px";
    dialogConfig.data = {
      action: "single costcenter",
    };

    const dialogRef = this.dialog.open(
      CcentreLookupComponent,
      dialogConfig
    );

    dialogRef.afterClosed().subscribe((result) => {
      if (result && result.data.length != 0) {
        console.log("result: ", result.data);

        this.costcenterIsSelected = true;

        this.mngForm.patchValue({
          costCenterCode: result.data[0].costCenterCode,
        });
      }
    });
  }
  //******************************************************************************************************************************** */
  cancel(): void {
    this.router.navigate(["/erp-admin/admin/all-requests"]);
  }

  submit() {
    console.log("this.mngForm.value: ", this.mngForm.value);
    if (this.pageFunction === "Add") {
      this.initiateRequestsService
        .addRequests(this.mngForm.value)
        .pipe(takeUntil(this.destroy$))
        .subscribe({
          next: (res) => {
            if (res) {
              this.snackbar.showNotification(
                "snackbar-success",
                "Request Added Successfully"
              );
              this.router.navigate(["/erp-admin/admin/all-requests"]);
            } else {
              this.snackbar.showNotification("snackbar-danger", res.message);
            }
          },
          error: (err) => {
            this.snackbar.showNotification("snackbar-danger", err);
          },
          complete: () => { },
        }),
        Subscription;
    } else if (this.pageFunction === "Update") {
      this.initiateRequestsService
        .updateRequests(this.mngForm.value)
        .pipe(takeUntil(this.destroy$))
        .subscribe({
          next: (res) => {
            if (res) {
              this.snackbar.showNotification(
                "snackbar-success",
                "Request Updated Successfully"
              );
              this.router.navigate(["/erp-admin/admin/all-requests"]);
            } else {
              this.snackbar.showNotification("snackbar-danger", res.message);
            }
          },
          error: (err) => {
            this.snackbar.showNotification(
              "snackbar-danger",
              err.message
            );
          },
          complete: () => { },
        }),
        Subscription;
    }
  }
}
