import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { Observable } from "rxjs";
import { environment } from "src/environments/environment.prod";

@Injectable({
  providedIn: "root",
})
export class PrivilegesService {
  constructor(private http: HttpClient) { }

  getPrivileges(): Observable<any> {
    const PrivilegesUrl = `${environment.baseUrlAdmin}/api/v1/allprivileges/all`;
    return this.http.get<any[]>(PrivilegesUrl);
  }

  getPrivilegesById(params: any): Observable<any> {
    return this.http.get<any>(
      `${environment.baseUrlAdmin}/api/v1/allprivileges/find/by/id`,
      { params }
    );
  }

  getPrivilegesByStatus(params): Observable<any> {
    const PrivilegesUrl = `${environment.baseUrlAdmin}/api/v1/allprivileges/fetchbyStatus`;
    return this.http.get<any[]>(PrivilegesUrl, { params });
  }

  addPrivilege(params: any, data: any): Observable<any> {
    const PrivilegesUrl = `${environment.baseUrlAdmin}/api/v1/allprivileges/add`;
    return this.http.post<any[]>(PrivilegesUrl, data, { params });
  }

  updatePrivilege(data: any): Observable<any> {
    const PrivilegesUrl = `${environment.baseUrlAdmin}/api/v1/allprivileges/modify`;
    return this.http.put<any[]>(PrivilegesUrl, data);
  }

  deletePrivilegeTemporarily(data: any): Observable<any> {
    console.log("data: ", data);
    const PrivilegesUrl =
      "http://52.15.152.26:6200/api/v1/allprivileges/delete";
    return this.http.post<any[]>(PrivilegesUrl, data);
  }

  uploadBatchPrivileges(data: any): Observable<any> {
    const PrivilegesUrl = `${environment.baseUrlAdmin}/api/v1/allprivileges/create/bulk`;
    return this.http.post<any>(PrivilegesUrl, data);
  }

  validatePrivileges(dataToValidate: any): Observable<any> {
    const imprestUrl = `${environment.baseUrlAdmin}/api/v1/allprivileges/update/state`;
    return this.http.put<any>(imprestUrl, dataToValidate);
  }

  getPrivielgesByParams(params: any): Observable<any> {
    const PrivilegessUrl = `${environment.baseUrlAdmin}/api/v1/allprivileges/read`;
    return this.http.get<any>(PrivilegessUrl, { params });
  }
}
