import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { Observable } from "rxjs";
import { environment } from "src/environments/environment.prod";



@Injectable({
    providedIn: "root",
})
export class EmailService {

    constructor(private http: HttpClient) { }



    getEmail(): Observable<any> {
        const EmailUrl = `${environment.baseUrlAdmin}/api/v1/auth/Email/all`;
        return this.http.get<any[]>(EmailUrl);
    }

    getEmailById(params: any): Observable<any> {
        return this.http.get<any>(
            `${environment.baseUrlAdmin}/api/v1/auth/Email/find/by/id`, { params }
        );
    }

    getEmailByStatus(params): Observable<any> {
        const EmailUrl = `${environment.baseUrlAdmin}/api/v1/auth/Email/fetchbyStatus`;
        return this.http.get<any[]>(EmailUrl, { params });
    }


    addEmail(data: any): Observable<any> {
        const EmailUrl = `${environment.baseUrlAdmin}/api/v1/auth/Email/create`;
        return this.http.post<any[]>(EmailUrl, data);
    }

    updateEmail(data: any): Observable<any> {
        const EmailUrl = `${environment.baseUrlAdmin}/api/v1/auth/Email/update`;
        return this.http.put<any[]>(EmailUrl, data);
    }

    deleteEmailTemporarily(params: any): Observable<any> {
        const EmailUrl = `${environment.baseUrlAdmin}/api/v1/auth/Email/delete`;
        return this.http.delete<any[]>(EmailUrl, { params });
    }


    uploadBatchEmail(data: any): Observable<any> {
        const EmailUrl = `${environment.baseUrlAdmin}/api/v1/auth/Email/create/bulk`;
        return this.http.post<any>(EmailUrl, data);
    }



    validateEmail(dataToValidate: any): Observable<any> {
        const imprestUrl = `${environment.baseUrlAdmin}/api/v1/auth/Email/update/state`;
        return this.http.put<any>(imprestUrl, dataToValidate);
    }





    // ********************************************************************************************************************



}
