import { SelectionModel } from "@angular/cdk/collections";
import { Component, OnInit, ViewChild } from "@angular/core";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { MatDialog } from "@angular/material/dialog";
import { MatMenuTrigger } from "@angular/material/menu";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { Router } from "@angular/router";
import { Subscription, Subject, takeUntil } from "rxjs";
import { AuthService } from "src/app/core/service/auth.service";
import { UsersManagementService } from "src/app/erp-admin/data/services/user-management.service";
import { AccessControlService } from "src/app/erp-procurement/data/services/_AccessControlService.service";
import { SnackbarService } from "src/app/shared/services/snackbar.service";
import Swal from "sweetalert2";

@Component({
  selector: "app-all-users",
  templateUrl: "./all-users.component.html",
  styleUrls: ["./all-users.component.scss"],
})
export class AllUsersComponent implements OnInit {
  //table columns
  displayedColumns: string[] = [
    "select",
    "id",
    "username",
    //"firstName",
    //"lastName",
    "empPfNo",
    "email",
    "phoneNumber",
    "roleName",
    "resendCredentials",
    "status",
    "isAcctActive",
    "action",
  ];

  //binding the columns to a table
  dataSource!: MatTableDataSource<any>;
  @ViewChild(MatPaginator) paginator!: MatPaginator;
  @ViewChild(MatSort) sort!: MatSort;

  contextMenu: MatMenuTrigger;
  contextMenuPosition = { x: "0px", y: "0px" };

  subscription!: Subscription;
  selection = new SelectionModel<any>(true, []);

  //necessary variables
  data: any;
  error: any;
  formData: any;
  isLoading = true;
  pageFunction = "ADD"; //default value

  //management of data destruction upon usage
    destroy$: Subject<boolean> = new Subject<boolean>();

  downloadLoading: boolean = false;

  Form: FormGroup;
  // ****************************************************************************************************************************

  constructor(
    private router: Router,
    private snackbar: SnackbarService,
    private usersManagementService: UsersManagementService,
    private accessControlService: AccessControlService,
    private dialog: MatDialog,
    private fb: FormBuilder,
    private authService: AuthService
  ) {}

  ngOnInit(): void {
    this.Form = this.fb.group({
      status: ["PENDING", Validators.required],
    });

    this.getData();
  }

  ngOnDestroy(): void {
    this.destroy$.next(true);
    this.destroy$.complete();
  }

  // ****************************************************************************************************************************
  // data filtering
  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  // ****************************************************************************************************************************

  reqStatuses: any[] = [
    { name: "PENDING" },
    // { name: "VERIFIED" },
    { name: "APPROVED" },
    { name: "RETURNED" },
    { name: "REJECTED" },
  ];

  selectedStatus: string = "PENDING";
  getSelectSelectedStatus() {
    this.getData();
  }
  getSelectedStatus(status: any) {
    this.Form.patchValue({
      status: status,
    });

    this.getData();
  }

  getData() {
    this.dataSource = new MatTableDataSource([]);
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
    this.isLoading = true;
    this.selectedStatus = this.Form.value.status;

    let params = {
      status: this.Form.value.status,
    };

    this.usersManagementService
      .getUsersByStatus(params)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (res) => {
          console.log("this.data :", res);
          if (res.entity) {
            this.data = res;
            this.isLoading = false;
            this.dataSource = new MatTableDataSource(this.data.entity);
            this.dataSource.paginator = this.paginator;
            this.dataSource.sort = this.sort;

            console.log("this.data :", this.data);
          } else {
            this.snackbar.showNotification("snackbar-danger", res.message);
          }
        },
        error: (err) => {
          this.snackbar.showNotification("snackbar-danger", err.message);
        },
        complete: () => {
          this.selection = new SelectionModel<any>(true, []);
        },
      }),
      Subscription;
  }

  refresh() {
    this.getData();
  }

  // ****************************************************************************************************************************
  //adding User
  addUser() {
    this.router.navigate(["/erp-admin/admin/manage-user"]);
  }

  //Updating the Users

  editUser(data: any) {
    let route = "/erp-admin/admin/manage-user";
    this.router.navigate([route], {
      queryParams: {
        id: data.id,
        action: "Update",
      },
    });
  }

  //Viewing the Users
  viewUser(data: any) {
    let route = "/erp-admin/admin/manage-user";
    this.router.navigate([route], {
      queryParams: {
        id: data.id,
        action: "View",
      },
    });
  }
  //deleting Users

  lockOrUnlockAccount(row) {
    let action = "";
    let parameter = "";

    console.log("row.isAcctActive:: ", row.isAcctActive)
  
    if (row.isAcctActive === "Y") {
      action = "Lock";
      parameter = "Lock";
    } else if (row.isAcctActive === "N") {
      action = "UnLock";
      parameter = "Restore";
    }
  
    Swal.fire({
      title: "Are you sure?",
      text: "This account will be " + action + "ed!",
      icon: "question",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, " + action + " this account",
    }).then((result) => {
      if (result.isConfirmed) {
        const params = { id: row.id, action: parameter };
        this.usersManagementService
          .accountLockUnlock(params)
          .pipe(takeUntil(this.destroy$))
          .subscribe({
            next: (res) => {
              console.log("res: ", res);
              if (res.statusCode === 201 || res.statusCode === 200 || res.statusCode === 302) {
                this.snackbar.showNotification("snackbar-success", res.message);
              } else {
                this.snackbar.showNotification("snackbar-danger", res.message);
              }
            },
            error: (err) => {
              this.snackbar.showNotification("snackbar-danger", err.message);
            },
            complete: () => {
              this.getData();
            },
          });
      }
    });
  }
  

  deleteUser(id: any) {
    Swal.fire({
      title: "Are you Sure?",
      text: "This User will be deleted!!",
      icon: "question",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      confirmButtonText: "Yes, Delete this User ",
    }).then((result) => {
      if (result.isConfirmed) {
        let params = { id: id };
        this.usersManagementService
          .deleteUserTemporarily(params)
          .pipe(takeUntil(this.destroy$))
          .subscribe({
            next: (res) => {
              console.log("res: ", res);
              if (res.statusCode == 201 || 200 || 302) {
                this.snackbar.showNotification("snackbar-success", res.message);
              } else {
                this.snackbar.showNotification("snackbar-danger", res.message);
              }
            },
            error: (err) => {
              this.snackbar.showNotification("snackbar-danger", err.message);
            },
            complete: () => {
              this.getData();
            },
          }),
          Subscription;
      }
    });
  }

  // ****************************************************************************************************************************

  //Select
  selectedRows: any[] = [];
  atleastOneSelected: boolean = false;

  /** Whether the number of selected elements matches the total number of rows. */
  isAllSelected() {
    const numSelected = this.selection.selected.length;
    const numRows = this.dataSource.data.length;
    return numSelected === numRows;
  }

  /** Selects all rows if they are not all selected; otherwise clear selection. */
  masterToggle() {
    if (this.isAllSelected()) {
      this.selection.clear();
      return;
    }

    this.selection.select(...this.dataSource.data);
  }

  /** The label for the checkbox on the passed row */
  checkboxLabel(row?: any): string {
    if (!row) {
      return `${this.isAllSelected() ? "deselect" : "select"} all`;
    }
    return `${this.selection.isSelected(row) ? "deselect" : "select"} row ${
      row.position + 1
    }`;
  }

  expSelected() {
    this.atleastOneSelected = true;
    this.selectedRows = this.selection.selected;

    console.log("this.selectedRows: ", this.selectedRows);
  }

  hasAccess: boolean;
  processMultipleRows(status: any) {
    let privilege = "Dashboard";
    //Validate Users

    this.hasAccess = this.accessControlService.hasPrivilege("AdminModule", [
      privilege,
    ]);

    if (this.hasAccess) {
      Swal.fire({
    title: '<span style="font-size: 20px;">Enter Verifier Remarks</span>',
    input: "textarea",
    inputPlaceholder: "Type your remarks here...",
    confirmButtonText: "Submit",
    showCancelButton: true,
    cancelButtonText: "Cancel",
    icon: "info",

    inputValidator: (value) => {
          if (!value) {
            return "Please enter your remarks";
          }
        },
      }).then((result) => {
        if (result.isConfirmed) {
          const verifierRemarks = result.value;

          const processedRows = this.selectedRows.map((element) => {
            return {
              id: element.id,
              status: status,
              verifierRemarks: verifierRemarks,
            };
          });

          console.log("LOG: ", processedRows);

          this.usersManagementService
            .validateUsers(processedRows)
            .pipe(takeUntil(this.destroy$))
            .subscribe({
              next: (res) => {
                console.log("res: ", res);
                if (res.statusCode == 201 || 200 || 302) {
                  this.snackbar.showNotification(
                    "snackbar-success",
                    res.message
                  );

                  this.selection = new SelectionModel<any>(true, []);
                } else {
                  this.snackbar.showNotification(
                    "snackbar-danger",
                    res.message
                  );
                }
              },
              error: (err) => {
                this.snackbar.showNotification("snackbar-danger", err.message);
              },
              complete: () => {
                this.getData();
              },
            }),
            Subscription;
        } else {
          this.selection = new SelectionModel<any>(true, []);
        }
      });
    }
  }

  resendEmail(row) {
    this.downloadLoading = true;
    if (row.email) {
      let body = {
        emailAddress: row.email,
      };
      this.authService
        .forgotPassword(body)
        .pipe(takeUntil(this.destroy$))
        .subscribe({
          next: (res) => {
            console.log("res: ", res);

            if (res.statusCode == 200) {
              this.snackbar.showNotification("snackbar-success", res.message);
            } else {
              this.snackbar.showNotification("snackbar-danger", res.message);
            }

            this.downloadLoading = false;
          },
          error: (err) => {
            console.log("err::: ", err);
            if (err.status === 401) {
              this.snackbar.showNotification(
                "snackbar-danger",
                "Failed: Please check the user's email and try again."
              );
            } else {
              this.snackbar.showNotification("snackbar-danger", err.message);
            }

            this.downloadLoading = false;
          },
          complete: () => { },
        }),
        Subscription;

      console.log("row.email: ", row.email);
    }
  }
}
