import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AllRolesComponent } from './pages/roles/all-roles/all-roles.component';
import { ManageRoleComponent } from './pages/roles/manage-role/manage-role.component';
import { RoutePrivilegeGuard } from 'src/app/erp-procurement/data/services/_AccessControlAuthGuard.service';
import { LoginSessionComponent } from './pages/login-session/login-session.component';
import { AccessRequestComponent } from './pages/access-requests/access-request/access-request.component';
import { ActiveUsersComponent } from './pages/active-users/active-users.component';
import { ApplicationLogsComponent } from './pages/application-logs/application-logs.component';
import { AccessRequestDetailsComponent } from './pages/access-requests/access-request-details/access-request-details.component';
import { AllRequestsComponent } from './pages/initiate-requests/all-requests/all-requests.component';
import { ManageRequestComponent } from './pages/initiate-requests/manage-request/manage-request.component';
import { AllUsersComponent } from './pages/user-management/all-users/all-users.component';
import { ManageUserComponent } from './pages/user-management/manage-user/manage-user.component';
import { PrivelegesComponent } from './pages/priveleges/priveleges.component';
import { ConfigurationsEmailComponent } from './pages/configurations-email/configurations-email.component';
import { AllDelegationsComponent } from './pages/priveleges-delegate/all-delegations/all-delegations.component';
import { ManageDelegationComponent } from './pages/priveleges-delegate/manage-delegation/manage-delegation.component';

const routes: Routes = [


  {
    path: "all-privileges",
    component: PrivelegesComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {  clientName: 'AdminModule', requiredPrivilege: ["Dashboard"] },

  },


  {
    path: "all-delegations",
    component: AllDelegationsComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {  clientName: 'AdminModule', requiredPrivilege: ["All Privilege Delegations", "Dashboard"] },

  },
  {
    path: "manage-delegations",
    component: ManageDelegationComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {  clientName: 'AdminModule', requiredPrivilege: ["Manage Privilege Delegation", "Dashboard"] },

  },

  {
    path: "email-configuration",
    component: ConfigurationsEmailComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {  clientName: 'AdminModule', requiredPrivilege: ["Dashboard"] },

  },

  //Roles
  {
    path: "all-roles",
    component: AllRolesComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {  clientName: 'AdminModule', requiredPrivilege: ["All Roles"] },

  },
  {
    path: "manage-role",
    component: ManageRoleComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {  clientName: 'AdminModule', requiredPrivilege: ["Manage Role"] },
  },

  //user mngt
  {
    path: "all-users",
    component: AllUsersComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {  clientName: 'AdminModule', requiredPrivilege: ["All Users","Dashboard"] },
  },
  {
    path: "manage-user",
    component: ManageUserComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {  clientName: 'AdminModule', requiredPrivilege: ["Manage Users","Dashboard"] },
  },


  //Requests

  {
    path: "all-requests",
    component: AllRequestsComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {  clientName: 'AdminModule', requiredPrivilege: ["All Requests"] },
  },
  {
    path: "manage-request",
    component: ManageRequestComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {  clientName: 'AdminModule', requiredPrivilege: ["All Requests","Manage Request"] },
  },



  {
    path: "access-request",
    component: AccessRequestComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {  clientName: 'AdminModule', requiredPrivilege: ["Access Request"] },
  },
  {
    path: "access-request-details",
    component: AccessRequestDetailsComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {  clientName: 'AdminModule', requiredPrivilege: ["Access Request Details"] },
  },


  {
    path: "active-users",
    component: ActiveUsersComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {  clientName: 'AdminModule', requiredPrivilege: ["Active Users"] },
  },
  {
    path: "login-session",
    component: LoginSessionComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {  clientName: 'AdminModule', requiredPrivilege: ["Login session"] },
  },
  {
    path: "application-logs",
    component: ApplicationLogsComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {  clientName: 'AdminModule', requiredPrivilege: ["Application Logs"] },
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AdminRoutingModule { }
